.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border: 1px solid grey;
  padding: 20px 40px;
  height: 250px;
}

.book {
  cursor: pointer;
}

.App > button {
  display: block;
  position: absolute;
  left: 10px;
  top: 10px;
}
